class Contact extends React.Component {
    state = {
        user_name: "",
        user_email: "",
        user_password: "",
        user_accept: false,
        message_send: "",
        errors: {
            user_name: false,
            user_email: false,
            user_password: false,
            user_accept: false
        }
    }
    messages = {
        user_name_incorrect: "Imię - minimum 3 znaki, nie może zawierać spacji",
        user_email_incorrect: "email - w adresie email brakuje @",
        user_password_incorrect: "wpisz treść wiadomości",
        user_accept_incorrect: "brak zgody - należy wyrazić wszelaką zgodę",
    }
    validationForm = () => {
        let user_name = false;
        let user_email = false;
        let user_password = false;
        let user_accept = false;
        let correct = false

        if (this.state.user_name.length >= 3 && this.state.user_name.indexOf(" ") === -1) {
            user_name = true;
        }
        if (this.state.user_email.indexOf("@") !== -1) {
            user_email = true;
        }
        if (this.state.user_password.length > 0) {
            user_password = true;
        }
        if (this.state.user_accept) {
            user_accept = true;
        }
        if (user_name && user_email && user_password && user_accept) {
            correct = true;
        }
        return { user_name, user_email, user_password, user_accept, correct }
    }
    handleSubmitApp12 = (e) => {
        e.preventDefault();
        const validation = this.validationForm();
        if (validation.correct) {
            this.setState({
                user_name: "",
                user_email: "",
                user_password: "",
                user_accept: false,
                message_send: "Wiadomość wysłana",
                errors: {
                    user_name: false,
                    user_email: false,
                    user_password: false,
                    user_accept: false
                }
            })
        } else {
            this.setState({
                message_send: "",
                errors: {
                    user_name: !validation.user_name,
                    user_email: !validation.user_email,
                    user_password: !validation.user_password,
                    user_accept: !validation.user_accept
                }
            })

        }
    }
    handleChangeApp12 = (e) => {
        const inputName = e.target.name;
        if (e.target.type === "checkbox") {
            return this.setState({
                [inputName]: e.target.checked
            })
        }
        this.setState({
            [inputName]: e.target.value
        })
    }
    render() {
        const { user_name, user_email, user_password, user_accept } = this.state;
        return (
            <div className="container bg-light">
                <h2 className='h2 ml-5 pt-5'>Skontaktuj się z nami</h2>
                <div className="row">
                    <div className="col-4 pt-5">
                        <img className='contact-logo' src={logo} alt="" />
                        <div className="h1 text-center">BartekShop</div>
                    </div>
                    <div className="col-8">
                        <form className='form-contact mb-5 p-3' onSubmit={this.handleSubmitApp12} noValidate>
                            <label className='form-label' htmlFor="name"> Twoje imię:
            <input className='form-input' type="text" id="name" name="user_name" value={user_name} onChange={this.handleChangeApp12} />
                            </label>
                            <label className='form-label' htmlFor="name"> Twój email:
            <input className='form-input' type="email" id="email" name="user_email" value={user_email} onChange={this.handleChangeApp12} />
                            </label>
                            <label className='form-label' htmlFor="password"> Twoja wiadomość:
                        <textarea className='form-area d-block ' type="text" id="password" name="user_password" value={user_password} onChange={this.handleChangeApp12} cols="60" rows="10"></textarea>
                            </label>
                            <label className='form-label' htmlFor="checkbox">
                                <input type="checkbox" id="checkbox" name="user_accept" checked={user_accept} onChange={this.handleChangeApp12} /> <i>Wyrażam zgodę wszelaką</i>
                            </label>
                            <button className='mb-5 px-5 py-2'>Wyślij</button>
                            {this.state.errors.user_name && <p className="red">{this.messages.user_name_incorrect}</p>}
                            {this.state.errors.user_email && <p className="red">{this.messages.user_email_incorrect}</p>}
                            {this.state.errors.user_password && <p className="red">{this.messages.user_password_incorrect}</p>}
                            {this.state.errors.user_accept && <p className="red">{this.messages.user_accept_incorrect}</p>}
                            {this.state.message_send && <h3 className="green h3">{this.state.message_send}</h3>}
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}
