const Page = () => {
    return (<>
        <Switch>
            <Route path='/' exact component={Home} />
            <Route path='/services' exact component={Services} />
            <Route path='/about' exact component={About} />
            <Route path='/contact' exact component={Contact} />
            <Route path='/fruits' exact component={Fruits} />
            <Route path='/vegetables' exact component={Vegetables} />
            <Route path='/spices' exact component={Spices} />
            <Route path='/oils' exact component={Oils} />
            <Route path='/seeds' exact component={Seeds} />
            <Route path='/' component={ErrorPage} />
        </Switch>
    </>);
}

export default Page;